
import { defineComponent } from "vue";

import { modalController } from "@ionic/vue";
import { COM20600112_RES, COM20600112_REQ, PortalServiceDetail, RoleList } from "@/shared/type/COM20600112.type";
import { COM20600121_RES, COM20600121_REQ } from "@/shared/type/COM20600121.type";
import { COM20600131_RES, COM20600131_REQ } from "@/shared/type/COM20600131.type";
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
    name: "COM206001SC01",
    components: {
        ModifyCard,
        BreadCrumb
    },
    data() {
        const modifyCard = {
            registerTellerId: '',
            registerTellerName: '',
            registerDate: '',
            registerTime: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
        }
        const portalServiceDetail = {
            firstRegisterTellerID: "",
            lastChangeBranchCode: "",
            firstRegisterTellerName: "",
            firstRegisterBranchCode: "",
            firstRegisterTime: "",
            lastChangeTime: "",
            roleList: [] as RoleList[],
            serviceIDName: "",
            channelCategoryCode: "",
            transactionControlStatusCode: "01",
            lastChangeTellerName: "",
            serviceID: "",
            lastChangeDate: "",
            firstRegisterDate: "",
            serviceIDDescription: "",
            lastChangeTellerID: "",
            checkServiceYN: "Y"
        } as PortalServiceDetail;
        const isEdit = true as boolean
        const isInValidInput = false as boolean

        return {
            portalServiceDetail,
            modifyCard,
            isEdit,
            isInValidInput,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
            isApprovalRegister: false
        };
    },

    async created() {
        if (this.approvalType === '') {
            if (this.$route.query.type == "register") {
                this.isEdit = false
                this.portalServiceDetail.transactionControlStatusCode = this.TransactionControlStatusCode[0].key
            } else {
                this.requestPortalServiceDetail();
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                let res: COM20600112_RES
                this.approvalRes = authDetail.body
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) !== 'register') {
                    this.isApprovalRegister = true
                    const reqBody = { serviceID: authDetailParsed.serviceID } as COM20600112_REQ;
                    res = await this.$request.getRequest("COM20600112", reqBody, false) as COM20600112_RES;
                } else {
                    this.isApprovalRegister = false
                    res = {} as COM20600112_RES
                }
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.portalServiceDetail = {
                    roleList: this.$util.checkServiceIdType(authDetail.body.serviceID) !== 'register' ? res.body.roleList : [],
                    serviceIDName: authDetailParsed.serviceIDName,
                    channelCategoryCode: authDetailParsed.channelCategoryCode,
                    transactionControlStatusCode: authDetailParsed.transactionControlStatusCode,
                    serviceID: authDetailParsed.serviceID,
                    serviceIDDescription: authDetailParsed.serviceIDDescription,
                    firstRegisterDate: '',
                    firstRegisterTime: '',
                    firstRegisterTellerName: '',
                    firstRegisterBranchCode: '',
                    firstRegisterTellerID: '',
                    lastChangeTellerName: '',
                    lastChangeTellerID: '',
                    lastChangeDate: '',
                    lastChangeBranchCode: '',
                    lastChangeTime: '',
                    checkServiceYN: authDetailParsed.checkServiceYN,
                }
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        TransactionControlStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.TransactionControlStatusCode
        }
    },

    methods: {
        async requestPortalServiceDetail() {
            const reqBody = { serviceID: this.$route.query.serviceID } as COM20600112_REQ;
            const res = await this.$request.getRequest("COM20600112", reqBody, false) as COM20600112_RES;
            if (res?.header.result) {
                this.portalServiceDetail = {
                    firstRegisterTellerID: res.body.firstRegisterTellerID,
                    lastChangeBranchCode: res.body.lastChangeBranchCode,
                    firstRegisterTellerName: res.body.firstRegisterTellerName,
                    firstRegisterBranchCode: res.body.firstRegisterBranchCode,
                    firstRegisterTime: this.$datetime.formatStringToTime(res.body.firstRegisterTime),
                    lastChangeTime: this.$datetime.formatStringToTime(res.body.lastChangeTime),
                    roleList: res.body.roleList,
                    serviceIDName: res.body.serviceIDName,
                    channelCategoryCode: res.body.channelCategoryCode,
                    transactionControlStatusCode: res.body.transactionControlStatusCode,
                    lastChangeTellerName: res.body.lastChangeTellerName,
                    serviceID: res.body.serviceID,
                    lastChangeDate: this.$datetime.formatStringToStringDate(res.body.lastChangeDate),
                    firstRegisterDate: this.$datetime.formatStringToStringDate(res.body.firstRegisterDate),
                    serviceIDDescription: res.body.serviceIDDescription,
                    lastChangeTellerID: res.body.lastChangeTellerID,
                    checkServiceYN: res.body.checkServiceYN,
                }
                this.modifyCard = {
                    registerTellerId: res?.body.firstRegisterTellerID,
                    registerTellerName: res?.body.firstRegisterTellerName,
                    registerDate: res?.body.firstRegisterDate,
                    registerTime: res?.body.lastChangeTime,
                    updatedTellerId: res?.body.lastChangeTellerID,
                    updatedTellerName: res?.body.lastChangeTellerName,
                    updatedDate: res?.body.lastChangeDate,
                    updatedTime: res?.body.lastChangeTime
                }
            }
        },

        async requestPortalServiceRegister() {
            const reqBody = {
                serviceIDDescription: this.portalServiceDetail.serviceIDDescription,
                serviceIDName: this.portalServiceDetail.serviceIDName,
                serviceID: this.portalServiceDetail.serviceID,
                checkServiceYN: this.portalServiceDetail.checkServiceYN
            } as COM20600121_REQ;
            const res = await this.$request.getRequest("COM20600121", reqBody, false) as COM20600121_RES;
            if (res.header.result) {
                this.$router.push("COM206001SC00");
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        async requestPortalServiceEdit() {
            const reqBody = {
                serviceIDName: this.portalServiceDetail.serviceIDName,
                serviceID: this.portalServiceDetail.serviceID,
                serviceIDDescription: this.portalServiceDetail.serviceIDDescription,
                transactionControlStatusCode: this.portalServiceDetail.transactionControlStatusCode,
                channelCategoryCode: "02",
                checkServiceYN: this.portalServiceDetail.checkServiceYN
            } as COM20600131_REQ;
            const res = await this.$request.getRequest("COM20600131", reqBody, false) as COM20600131_RES;
            if (res.header.result) {
                this.isEdit = true;
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.requestPortalServiceDetail()
                        modalController.dismiss()
                    }
                });
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case "edit":
                    this.isEdit = false;
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: this.$route.query.type === 'edit' ? this.$t('message.cancel_without_saving_changes') : this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.requestPortalServiceDetail();
                                this.isEdit = true;
                            } else {
                                this.$router.push("COM206001SC00");
                            }
                        },
                    });
                    break;
                case "save":
                    if (
                        this.portalServiceDetail.serviceID &&
                        this.portalServiceDetail.serviceIDName &&
                        this.portalServiceDetail.serviceIDDescription &&
                        this.portalServiceDetail.transactionControlStatusCode &&
                        this.portalServiceDetail.checkServiceYN
                    ) {
                        this.$dialog.onOpen({
                            content: `${this.$t("message.would_you_like_to_save")}`,
                            dialogMode: "Question",
                            callback: () => {
                                if (this.$route.query.type == "edit") {
                                    this.requestPortalServiceEdit();
                                } else {
                                    this.requestPortalServiceRegister();
                                }
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                    }
                    break;
                default:
                    this.$router.push("COM206001SC00");
                    break;
            }
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        }
    },
});
